<template>
<div class="dropdown" ref="dropdownRef">
  <a href="#" class="btn btn-outline-light my-2 dropdown-toggle" @click.prevent="toggleOpen">
    {{title}}
  </a>
  <ul class="dropdown-menu" :style="{display: 'block'}" v-if="isOpen">
    <slot></slot>
  </ul>
</div>
</template>

<script lang="ts">
import { defineComponent, ref, watch, onUnmounted } from 'vue'
// vue3移除了$on,$off，使用 mitt 代替event bus
import mitt from 'mitt'
import useClickOutside from '../hooks/useClickOutside'
export const emitter = mitt()
export default defineComponent({
  name: 'Dropdown',
  props: {
    title: {
      type: String,
      required: true
    }
  },
  emits: ['item-clicked'],
  setup(props, context) {
    const isOpen = ref(false)
    const dropdownRef = ref<null | HTMLElement>(null)
    const toggleOpen = () => {
      isOpen.value = !isOpen.value
    }
    const dropDownItemClicked = (args: any) => {
      if (args.props.closeAfterClick) {
        isOpen.value = false
      }
      context.emit('item-clicked', args)
    }
    emitter.on('dropdown-item-clicked', dropDownItemClicked)
    onUnmounted(() => {
      emitter.off('dropdown-item-clicked', dropDownItemClicked)
    })
    // 实现鼠标点击下拉框外时，下拉框隐藏
    const isClickOutside = useClickOutside(dropdownRef)

    watch(isClickOutside, () => {
      if (isOpen.value && isClickOutside.value) {
        isOpen.value = false
      }
    })
    return {
      isOpen,
      toggleOpen,
      dropdownRef
    }
  }
})
</script>
