
import { defineComponent, reactive, PropType, onMounted, computed, inject } from 'vue'
import { emitter } from './ValidateForm.vue'
const emailReg = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
// 组件根节点传入的rules数组的元素的类型
interface RuleProp {
  type: 'required' | 'email' | 'custom' | 'range';
  message?: string;
  validator?: () => boolean;
  min?: {length: number; message: string };
  max?: {length: number; message: string };
}
export type RulesProp = RuleProp[]
export type TagType = 'input' | 'textarea'
export default defineComponent({
  props: {
    rules: Array as PropType<RulesProp>,
    modelValue: String,
    tag: {
      type: String as PropType<TagType>,
      default: 'input'
    }
  },
  // 避免父组件上非props的attribute作用在子组件的根节点上，配合v-bind="$attrs",使attributes绑定到期望的元素上
  inheritAttrs: false,
  setup(props, context) {
    const inputVal = computed({
      get: () => props.modelValue || '',
      set: val => {
        context.emit('update:modelValue', val)
      }
    })
    const inputRef = reactive({
      error: false,
      message: ''
    })
    const clearInput = () => {
      inputVal.value = ''
    }
    const validateInput = () => {
      if (props.rules) {
        const allPassed = props.rules.every(rule => {
          let passed = true
          inputRef.message = rule.message || ''
          const { value } = inputVal
          switch (rule.type) {
            case 'required':
              passed = (value.trim() !== '')
              break
            case 'email':
              passed = emailReg.test(value)
              break
            case 'range': {
              const { min, max } = rule
              if (min && value.trim().length < min.length) {
                passed = false
                inputRef.message = min.message
              }
              if (max && value.trim().length > max.length) {
                passed = false
                inputRef.message = max.message
              }
              break
            }
            case 'custom':
              passed = rule.validator ? rule.validator() : true
              break
            default:
              break
          }
          return passed
        })
        inputRef.error = !allPassed
        return allPassed
      }
      return true
    }
    onMounted(() => {
      emitter.emit('form-item-created', {
        validator: validateInput,
        clearInput,
        formName: inject('formName')
      })
    })
    return {
      inputRef,
      validateInput,
      inputVal
    }
  }
})
